.features-navigation {
  position: fixed;
  z-index: $z-index-9;
  bottom: 0;
  left: 0;
  width: 100%;
  height: auto;
  grid-row: 2 / 3;
  padding: $gap-100 0 $gap-600;
  opacity: 0;
  pointer-events: none;
  transition: opacity .25s;
  @include r(1499) {
    padding: $gap-100 0 $gap-300; }
  @include r(1023) {
    grid-row: 1 / 2; }
  @include r(767) {
    display: none; }
  &--active {
    opacity: 1;
    pointer-events: auto; }
  &__list {
    display: flex;
    justify-content: space-between;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 4px;
      filter: opacity(10%);
      background-color: var(--features-bg-color); } }
  &__list-item {
    padding-top: $gap-200;
    position: relative;
    width: calc(100% / 8);
    opacity: 0;
    transition: opacity $transition-100;
    @include r(1919) {
      padding-top: $gap-100; }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      transform: translateY(-25%);
      width: 100%;
      height: 6px;
      background-color: var(--features-bg-color);
      border-radius: 10px; }
    @include r(1023) {
      display: none;
      &:nth-child(1) {
        &::before {
          transform: translate(0, -25%); } }
      &:nth-child(2) {
        &::before {
          transform: translate(100%, -25%); } }
      &:nth-child(3) {
        &::before {
          transform: translate(200%, -25%); } }
      &:nth-child(4) {
        &::before {
          transform: translate(300%, -25%); } }
      &:nth-child(5) {
        &::before {
          transform: translate(400%, -25%); } }
      &:nth-child(6) {
        &::before {
          transform: translate(500%, -25%); } }
      &:nth-child(7) {
        &::before {
          transform: translate(600%, -25%); } }
      &:nth-child(8) {
        &::before {
          transform: translate(700%, -25%); } } }
    &--active {
      opacity: 1;
      @include r(1023) {
        display: flex; } } }
  &__list-item-block {
    display: flex;
    align-items: center;
    @include r(1023) {
      width: 100vw; } }
  &__list-item-icon {
    width: 40px;
    height: 40px;
    margin-right: 5px;
    @include r(1919) {
      width: 25px;
      height: 25px; } }
  &__item-text {
    @include rb;
    color: $blue-500;
    font-size: rem(17);
    white-space: nowrap;
    @include r(1023) {
      font-size: rem(30); } } }
